                </div>
            </div>
            <div class="buttons-block">
                <div class="dom">DOM</div>
                <button class="basic-button" @click="bindDOM()">
                    <img class="fox" src="@/assets/newImages/fox_corners.svg">
                    <p class="button-title">{{$localizeFilter('confirm')}}</p>
                </button>
            </div>
        </div>
    </template>

    <template v-if="state=='metamask_not_found'">
        <div class="metamask-not-found">
            <div class="dom-window">
                <div class="dom-window-content">
                    <p class="dom-window-text" v-html="$localizeFilter('bind_dom_text')"></p>
                </div>
            </div>
            <div class="buttons-block">
                <div class="dom">DOM</div>
                <button class="basic-button" @click="bindDOM()">
                    <img class="fox" src="@/assets/newImages/fox_corners.svg">
                    <p class="button-title"  @click="openLink">{{$localizeFilter('instruction')}}</p>
                </button>
            </div>
        </div>
    </template>

    <template v-if="state=='dom_binded'">
        <div class="dom-binded">
            <div class="dom-window">
                <div class="dom-window-content">
                    <div class="dom-window-text-2" v-html="$localizeFilter('dom_amount', [DOMAmount])"></div>
                    <div class="dom-window-text-2" v-html="$localizeFilter('metamask_wallet', [DOMWallet])"></div>
                </div>
            </div>
