<script>
import { defineComponent } from '@vue/composition-api'
import Web3 from 'web3'

export default defineComponent({
    props: {
        DOMWallet: {
            type: String,
            default: null
        },
        DOMAmount: {
            type: Number,
            default: null
        },
        clientId: {
            type: Number,
            default: null
        }
    },
    computed: {
        state() {
            if (this.DOMWallet) {
                return 'dom_binded'
            } else {
                return (window.ethereum && (window.ethereum.isMetaMask || window.ethereum.isTrust)) ? 'dom_not_binded' : 'metamask_not_found'
            }
        },
    },
    methods: {
        openLink(){
            window.open('https://telegra.ph/PRIVYAZKA-KRIPTO-KOESHELKOV-S-LICHNYM-KABINETOM-CAPITAL-WHALE-06-19', '_blank');
        },
        async bindDOM() {
            // console.log(window.ethereum)
            if(window.ethereum && (window.ethereum.isMetaMask || window.ethereum.isTrust)) {
                let web3 = new Web3(window.ethereum);
                // // console.log(web3)

                const accounts = await ethereum.request({ method: 'eth_requestAccounts' });
                const address = accounts[0];
                // // console.log(account, accounts)

                let message = `CapitalWhale User Id: ${this.clientId}`;

                let signature = (await web3.eth.personal.sign(web3.utils.fromUtf8(message), address));
                // let recoreved = await web3.eth.personal.ecRecover(message, signature);

                try {
                    // console.log('here')
                    const formData = {address, signature}
                    await this.$store.dispatch('bindDOM', formData)
                    this.$message(this.$localizeFilter('link_dom_success'))
                    this.$emit('domBinded')
                } catch(e) {
                    this.$error(e)
                } 
                

                // // console.log('address:', recoreved)
            }
        }

    },
    setup() {
        // // console.log(window.ethereum)
        
    },
})
</script>

