<script>
import { defineComponent } from '@vue/composition-api'
import localizeFilter from '@/filters/localize.filter'
import DOMWindow from '@/components/DOMWindow'

export default defineComponent({
    data: () => ({
        user: {
        },
        isEditing: false
    }),
    components: {
        DOMWindow
    },
    methods: {
        async contactSponsor() {
            try {
                const formData = {userId: this.user.sponsorId}
                const chatId = await this.$store.dispatch('getRoomWithUserId', formData)
                this.$router.push(`/cabinet/messenger?chatId=${chatId}`)
            } catch(e) {
                this.$error(e)
            } 
        },
        startEditing() {
            this.isEditing = true
            const firstInput = document.getElementById('last-name')
            firstInput.focus()
        },
        async endEditing() {
            try {
                const formData = {firstName: this.user.firstName, lastName: this.user.lastName, telegramName: this.user.telegramName}
                await this.$store.dispatch('editProfile', formData)
                this.$message(localizeFilter('profile_updated'))
                this.isEditing = false
            } catch(e) {
                this.$error(e)
            } 
        },
        domBinded(){
            this.loadData();
        },
         copy(value) {
            function fallbackCopyTextToClipboard(text) {
                var textArea = document.createElement("textarea");
                textArea.value = text;
                
                // Avoid scrolling to bottom
                textArea.style.top = "0";
                textArea.style.left = "0";
                textArea.style.position = "fixed";

                document.body.appendChild(textArea);
                textArea.focus();
                textArea.select();
                var successful;
                try {
                    successful = document.execCommand('copy');
                    var msg = successful ? 'successful' : 'unsuccessful';
                    // console.log('Fallback: Copying text command was ' + msg);
                } catch (err) {
                    console.error('Fallback: Oops, unable to copy', err);
                }

                document.body.removeChild(textArea);
                return successful;
            }
            if (value) {
                const clipboardData =
                // event.clipboardData ||
                window.clipboardData ||
                // event.originalEvent?.clipboardData ||
                navigator.clipboard;
                try {
                    clipboardData.writeText(value); 
                    this.$message(localizeFilter('copied')) 
                } catch (e) {
                    if(fallbackCopyTextToClipboard(value)){
                        this.$message(localizeFilter('copied')) 
                    }
                }
            }
        },
        async loadData() {
            try {
                let user = await this.$store.dispatch('getProfile')
                user.referralLink=`https://${location.host}/#/register?token=${user.referralToken}`
                this.user = user
            } catch(e) {
                this.$error(e)
            } 
        },
    },
    
    mounted() {
        this.loadData();
    }
})
</script>




